.header {
  width: 100%;
  height: 5rem;
  padding: 0 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #252424;
}

.header h1 {
  color: white;
}

.header ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media screen and (max-width:800px) {
  .header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 10rem;
    padding: .5rem 2rem;
    gap: .5rem;
    width: 100%;
  }

  .header h1 {
    margin: 0;
  }
}